import infoBox from "../components/boxstyle/infobox.vue"
export default {
	data() {
		return {
			// imgUrl: ["../../static/img/shouhuocha.png",
			// 	"../../static/img/guanlicar.png",
			// 	"../../static/img/guanli.png",
			// 	"../../static/img/zhuxiao.png"
			// ],
			// contentList: [
			// 	"收货人运单查询",
			// 	"车主车辆管理",
			// 	"司机车辆管理",
			// 	"注销登录"
			// ],
			pageList: [1, 2, 3, 4],
			userStatus: '',
			master: false,
			driver: false,
			consignee: false
		}
	},

	methods: {
		status() {
			const status = uni.getStorageSync("USER_STATUS");
			if (status === 'MASTER') {
				this.master = true
			}
			if (status === 'DRIVER') {
				this.driver = true
			}
			if (status === 'CONSIGNEE') {
				this.consignee = true
			}
		},

		toOtherPage(i) {
			if (i === 0) {
				if (this.userStatus === "CONSIGNEE") {
					uni.navigateTo({
						url: '/pages/user/waybill/waybill'
					});
				} else {
					uni.showToast({
						title: "请注销切换至收货人身份",
						icon: "none"
					})
				}
			}
			// i === 0 && this.userStatus === "CONSIGNEE" &&
			// // i === 0 &&
			// 	uni.navigateTo({
			// 		url: '/pages/user/waybill/waybill'
			// 	});

			if (i === 1) {
				if (this.userStatus === "MASTER") {
					uni.navigateTo({
						url: '/pages/user/carower/managementcar'
					});
				} else {
					uni.showToast({
						title: "请注销切换至车主身份",
						icon: "none"
					})
				}
			}

			// i === 1 && this.userStatus === "MASTER" &&
			// // i === 1 && 
			// 	uni.navigateTo({
			// 		url: '/pages/user/carower/managementcar'
			// 	});

			if (i === 2) {
				if (this.userStatus === "DRIVER") {
					uni.navigateTo({
						url: '/pages/user/carmanagement/carmanagement'
					});
				} else {
					uni.showToast({
						title: "请注销切换至司机身份",
						icon: "none"
					})
				}
			}

			// // i === 2 && this.userStatus ===  "DRIVER" &&
			// // i === 2 &&
			// 	uni.navigateTo({
			// 		url: '/pages/user/carmanagement/carmanagement'
			// 	});

			i === 3 &&
				uni.setStorage({
					key: 'USER_TOKEN',
					data: ""
				}) &&
				uni.reLaunch({
					url: "/pages/login/status"
				}) && this.toExit();
		},

		async toExit() {
			const opts = {
				url: "/personal/waybill/exit",
				method: 'post'
			}
			const res = await this.$http.httpTokenRequest(opts);
			console.log(res)
		},

		getStatus() {
			const status = uni.getStorageSync("USER_STATUS");
			this.userStatus = status;
			console.log(status);
		}

	},

	onLoad() {
		this.getStatus(),
			this.status()
	},

	components: {
		infoBox
	}


}
